/* @media (prefers-color-scheme: light) { */
:root {
  --background-color: white;
  --landing-grey: #eceef8;
  --highlight-color: #fff;
  --shadow-color: #3334;
  --dots-one-image: radial-gradient(#555 0.8px, #ffffff 0.8px);
  --dots-one-size: 10px 10px;
  --dots-two-image: radial-gradient(#555 0.8px, #ffffff 0.8px);
  --dots-two-size: 8px 8px;
  --crosshatch-background: radial-gradient(circle, transparent 30%, #fff 26%),
    linear-gradient(45deg, transparent 46%, #eee 47%, #eee 52%, transparent 53%),
    linear-gradient(
      135deg,
      transparent 46%,
      #eee 47%,
      #eee 52%,
      transparent 53%
    );
  --crosshatch-size: 50px 50px;
  --diagonal-background: repeating-linear-gradient(
    45deg,
    #eee,
    #eee 1px,
    #ffffff 1px,
    #ffffff 10px
  );
}
/* } */

