@font-face {
  font-family: "happy";
  src: url("./media/fonts/kghappy-webfont.woff2") format("woff2"),
    url("./media/fonts/kghappy-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "bouncy";
  src: url("./media/fonts/bouncy-personal_use_only-webfont.woff2")
      format("woff2"),
    url("./media/fonts/bouncy-personal_use_only-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

h1 {
  margin: 0;
  color: #000;
  font-size: min(70px, 12vw);
  /* font-size: 70px; */
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 200;
}

.happy {
  position: absolute;
  margin: 0;
  color: #555;
  font-size: 25px;
  font-family: happy;
  font-weight: 200;
  visibility: hidden;
}

.bouncy {
  position: absolute;
  margin: 0;
  color: #888;
  font-size: 25px;
  font-family: bouncy;
  font-weight: 200;
  visibility: hidden;
}

button {
  background-color: transparent;
  border-style: none;
}

.landing {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* background-color: var(--background-color); */
  transition: background-color 0.5s ease;
  overflow-x: hidden;
  background: var(--crosshatch-background);
  background-size: var(--crosshatch-size);
  opacity: 1;
}

.landing .cover {
  position: absolute;
  height: 100vh;
  width: 100vw;
  background-color: #fff;
  opacity: 1;
  transition: opacity 0.5s ease;
  transition-delay: 0.3s;
}

.landing .cover-animate {
  opacity: 0;
}

@keyframes wave-transition {
  0% {
    background-color: #fff;
    height: 200px;
    opacity: 1;
  }
  100% {
    background-color: #eee;
    height: max(150vh, 150vw);
    opacity: 0;
  }
}

.landing .wave {
  position: absolute;
  height: 200px;
  aspect-ratio: 1 / 1;
  border-radius: max(200vw, 200vh);
  translate: 0 -50px;
  opacity: 1;
  /* height: 100vh;
  width: 100vw; */
  /* background-color: #eee; */
  /* transition: background-color 0.5s ease-in; */
}

.landing .wave-animate {
  animation: wave-transition 0.5s ease-in forwards;
}

@keyframes circle-transition {
  0% {
    visibility: visible;
    background-color: #fff;
  }
  10% {
    box-shadow: 0px 0px 0px var(--shadow-color),
      inset 3px 3px 6px var(--shadow-color);
  }
  99% {
    visibility: visible;
    box-shadow: 0px 0px 0px var(--shadow-color),
    inset 3px 3px 6px var(--shadow-color);
  }
  100% {
    visibility: hidden;
    box-shadow: 0px 0px 0px var(--shadow-color),
    inset 3px 3px 6px var(--shadow-color);
  }
}

.landing .circle {
  /* position: relative; */
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
  width: 200px;
  border-radius: 100px;
  translate: 0 -50px;
  transition: box-shadow 0.5s ease;
  -webkit-transition: box-shadow 0.5s ease;
  cursor: pointer;
  z-index: 10;
  background-color: white;
}

.landing .animate-circle {
  box-shadow: 4px 4px 10px var(--shadow-color),
    inset 0px 0px 0px var(--shadow-color);
}

.landing .circle:active {
  box-shadow: 0px 0px 0px var(--shadow-color),
    inset 3px 3px 6px var(--shadow-color);
  background-color: #fff;
  transition: box-shadow 0.1s ease;
  -webkit-transition: box-shadow 0.1s ease;
}

.landing .disabled {
  box-shadow: 0px 0px 0px var(--shadow-color),
    inset 3px 3px 6px var(--shadow-color);
  transition: box-shadow 0.1s ease;
  -webkit-transition: box-shadow 0.1s ease;
  pointer-events: none;
  animation: circle-transition 1s linear forwards;
  /* animation-delay: 2.5s; */
}

.landing .circle-animate {
  animation: circle-transition 2s ease forwards;
  animation-delay: 4s;
}

.circle .push {
  margin: 0;
  font-size: 50px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 200;
  background-color: #fff;
  color: transparent;
  transition: background-color 0.5s ease, opacity 0.5s ease-in;
  text-shadow: 1px 1px 3px #fffd;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

.circle .animate-push {
  background-color: #ddd;
}

@keyframes flash {
  0% {
    visibility: hidden;
  }
  1% {
    visibility: visible;
  }
  99% {
    visibility: visible;
  }
  100% {
    visibility: hidden;
  }
}

.circle .click {
  rotate: -40deg;
  translate: -90px -90px;
}

.circle .click-animate {
  animation: flash 0.2s linear forwards;
}

.landing .outline {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 195px;
  width: 195px;
  border-radius: 100px;
  border-width: 2px;
  border-color: #ddd;
  translate: 0 -50px;
  background-color: none;
  border-style: dashed;
  z-index: 0;
  opacity: 1;
  transition: opacity 0.5s linear;
}

.landing .pop {
  visibility: visible;
  rotate: 10deg;
  /* animation: flash 0.2s linear forwards; */
}

.landing .hidden {
  visibility: hidden;
}

.landing .fade-out {
  opacity: 0;
}

/* .landing .animate-fill {
  animation: progress 2.5s ease forwards;
  animation-delay: 0.5s;
} */

.landing .top-outer-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  aspect-ratio: 1 / 1;
  width: min(80vh, 140vw, 1000px);
  border-radius: 1000px;
  background-color: var(--background-color);
  background-image: var(--dots-one-image);
  background-size: var(--dots-one-size);
  background-repeat: repeat;
  translate: min(200%, 30vw) -35%;
  overflow: hidden;
  top: 0;
  opacity: 0;
}

.landing .top-middle-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1 / 1;
  height: 50%;
  border-radius: 50%;
  background: #fff;
  overflow: hidden;
  transition: inherit;
  transition-delay: box-shadow;
  opacity: 1;
  -webkit-background-clip: content-box;
  -moz-background-clip: content-box;
  background-clip: content-box;
  color: transparent;
}

.landing .top-inner-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1 / 1;
  height: 85%;
  border-radius: 85%;
  overflow: hidden;
  transition: inherit;
  transition-delay: box-shadow;
  opacity: 1;
  -webkit-background-clip: content-box;
  -moz-background-clip: content-box;
  background-clip: content-box;
  background: radial-gradient(circle, transparent 30%, #fff 26%),
    linear-gradient(45deg, transparent 46%, #eee 47%, #eee 52%, transparent 53%),
    linear-gradient(
      135deg,
      transparent 46%,
      #eee 47%,
      #eee 52%,
      transparent 53%
    );
  background-size: 50px 50px;
}

.landing .top-center-outer-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1 / 1;
  height: 35%;
  border-radius: 100%;
  background-color: #ffffff;
  opacity: 1;
  background-image: var(--dots-two-image);
  background-size: var(--dots-two-size);
  background-repeat: repeat;
  overflow: hidden;
  opacity: 0;
  transition: inherit;
  /* transition-delay: 1.6s, 3s; */
  transition-delay: 0.6s;
  transition-property: opacity, box-shadow;
  /* box-shadow: 6px 6px 16px var(--shadow-color); */
  margin: 0 30% 20% 0;
}

.landing .top-center-middle-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1 / 1;
  height: 60%;
  border-radius: 100%;
  background-color: #fff;
  overflow: hidden;
  opacity: 1;
  transition: inherit;
  /* transition-delay: 1.8s, 3s; */
  transition-delay: 0.6s;
  /* transition-property: opacity, box-shadow; */
  transition-delay: box-shadow;
  /* box-shadow: 6px 6px 16px var(--shadow-color); */
}

.landing .top-center-inner-circle {
  aspect-ratio: 1 / 1;
  height: 60%;
  border-radius: 100%;
  background: #fff;
  overflow: hidden;
  opacity: 0;
  transition: inherit;
  transition-delay: 0.6s;
  transition-property: box-shadow;
  opacity: 1;
}

.landing .middle-outer-circle {
  position: absolute;
  left: 10vw;
  bottom: calc(25vh + min(20vw, 125px, 15vh));
  display: flex;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1 / 1;
  height: min(130px, 30vw);
  border-radius: 150px;
  transition-delay: 0.9s;
  opacity: 0;
}

.landing .middle-inner-circle {
  aspect-ratio: 1 / 1;
  height: 80%;
  border-radius: 150px;
  background-image: var(--dots-two-image);
  background-size: var(--dots-two-size);
  opacity: 0;
}

.landing .name-plate {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  aspect-ratio: 4.1 / 1;
  /* height: 125px; */
  height: min(20vw, 125px, 15vh);
  /* width: min(70vw, 500px, 125vh); */
  border-radius: 0 150px 150px 0;
  background-color: #fff;
  background: var(--diagonal-background);
  left: 0;
  bottom: 16vh;
  opacity: 0;
  /* transition: opacity 1s ease, box-shadow 1s ease; */
  /* transition-delay: 2.6s, 3s; */
  transition-delay: 1.2s;
  transition-property: opacity, box-shadow;
}

.landing .shadow-appear {
  opacity: 1;
  box-shadow: 3px 3px 8px var(--shadow-color);
}

.landing .tall-shadow-appear {
  opacity: 1;
  box-shadow: 6px 6px 16px var(--shadow-color);
}

.landing .inset-shadow-appear {
  opacity: 1;
  box-shadow: inset 3px 3px 8px var(--shadow-color);
}

.landing .tall-inset-shadow-appear {
  opacity: 1;
  box-shadow: inset 6px 6px 16px var(--shadow-color);
}

.landing .text-shadow {
  opacity: 1;
}

.content {
  display: none;
  height: 100vh;
}

.display-content {
  display: flex;
  background: var(--background-blue);
}
